import { Program, AnchorProvider } from "@project-serum/anchor";
import { Connection, clusterApiUrl, PublicKey, SystemProgram } from "@solana/web3.js";
import { TOKEN_PROGRAM_ID, ASSOCIATED_TOKEN_PROGRAM_ID } from "@solana/spl-token";
import idl from "./presale.json";
import nftIdl from "./nft_mint.json"; // Single import for NFT mint IDL
import smartContractIdl from "./smart_contract.json"; // Import your smart contract IDL

// Set your presale program ID.
const PRESALE_PROGRAM_ID = new PublicKey("7U3o2J5PzR7sTuYNTkyDybzmmk1Q3wVgMhgWq3euUHvt");

// Update the presale IDL metadata address.
idl.metadata.address = PRESALE_PROGRAM_ID.toString();

// Create a connection.
const connection = new Connection(clusterApiUrl("devnet"), {
  commitment: "confirmed",
  confirmTransactionInitialTimeout: 60000,
});

const getProvider = (wallet) => {
  return new AnchorProvider(connection, wallet, {
    preflightCommitment: "confirmed",
    commitment: "confirmed",
  });
};

const getPresaleProgram = (wallet) => {
  const provider = getProvider(wallet);
  return new Program(idl, PRESALE_PROGRAM_ID, provider);
};

// NFT Mint Program Helper
const NFT_MINT_PROGRAM_ID = new PublicKey("FGQK2DvAEMzmCeroPE6PHy9Ly9UZdCyqbG9uKUKFX1a2");

const getNFTMintProgram = (wallet) => {
  const provider = getProvider(wallet);
  return new Program(nftIdl, NFT_MINT_PROGRAM_ID, provider);
};

// Smart Contract Program Helper
const SMART_CONTRACT_PROGRAM_ID = new PublicKey("673wrbdb5yPfA7tChHusjVYm6aGhah2D1UdzdAjgjjCg");

// Ensure smartContractIdl.metadata exists before setting the address
if (!smartContractIdl.metadata) {
  smartContractIdl.metadata = {};
}
smartContractIdl.metadata.address = SMART_CONTRACT_PROGRAM_ID.toString();

const getSmartContractProgram = (wallet) => {
  const provider = getProvider(wallet);
  return new Program(smartContractIdl, SMART_CONTRACT_PROGRAM_ID, provider);
};

export {
  connection,
  getProvider,
  getPresaleProgram,
  PRESALE_PROGRAM_ID,
  TOKEN_PROGRAM_ID,
  ASSOCIATED_TOKEN_PROGRAM_ID,
  SystemProgram,
  getNFTMintProgram,
  NFT_MINT_PROGRAM_ID,
  getSmartContractProgram,
  SMART_CONTRACT_PROGRAM_ID,
};
